import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';

import {
	Panel,
	PanelHeader,
	PanelHeaderBack,
	Group,
	SimpleCell
}	from '@vkontakte/vkui';

import {
	Icon28NameTagOutline,
	Icon28MessagesOutline,
	Icon28FavoriteOutline,
	Icon28WarningTriangleOutline,
	Icon28EmployeeOutline
}	from '@vkontakte/icons';

const Admin = props => {
	const [datas, setDatas] = useState(null);
	useEffect(() => {
		fetch("http://bots.boilama.xyz/ntp/stat/user.php?key=pJR295F0PqIHDTBYHgs6BBfGG&user=266890472")
		.then(data => data.json())
		.then(data => {
		 if (data) {
		  setDatas(data.ball)
		  console.log(data)
		 } else {
		  console.log('ошибка')
		 }
		})
		.catch(err => {
		 console.log(err)
		})
		 }, [ ] );
	return (
	<Panel id={props.id}>
		<PanelHeader left={<PanelHeaderBack onClick={props.go} data-to="home"/>}>Карточка</PanelHeader>
		<Group>
            <SimpleCell before={<Icon28NameTagOutline />} description="описание">{datas}</SimpleCell>
			<SimpleCell before={<Icon28MessagesOutline />} description="Символы">BL</SimpleCell>
			<SimpleCell before={<Icon28FavoriteOutline />} description="Рейтинг">0</SimpleCell>
			<SimpleCell before={<Icon28WarningTriangleOutline />} description="Предупреждений">0</SimpleCell>
			<SimpleCell before={<Icon28EmployeeOutline />} description="Роль">Администратор</SimpleCell>
          </Group>
	</Panel>
	);
};

Admin.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Admin;